.nav__list {
  display: none;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
  display: flex;
  justify-content: space-between;
  width: 436px;
    }

  @media (min-width: @tablet-width) {
  width: 475px;
    }
  }

.nav__item {
  .list-reset();
  font-family: "Oswald", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  }

.nav__button-header {
  @media (min-width: @tablet-width) {
  display: none;
    }
  }

  @media (min-width: @desktop-width) {
.main-nav__wrapper {
  width: 475px;
    }
  }
